import "core-js/modules/es.array.push.js";
import { mapGetters } from 'vuex';
import DashboardTabs from '@/components/DashboardTabs.vue';
import SysConfigTabs from '@/components/SysConfigTabs.vue';
import SysCogUpload from '@/components/SysCogUpload.vue';
import SysCogSecurity from '@/components/SysCogSecurity.vue';
import SysCogPage from '@/components/SysCogPage.vue';
import SysCogOthers from '@/components/SysCogOthers.vue';
export default {
  name: 'SystemConfig',
  data() {
    return {
      activeIndex: 'upload',
      isSidebarCollapse: false
    };
  },
  watch: {
    // ÁõëÂê¨ÈîöÁÇπÂèòÂåñ
    '$route.hash': {
      immediate: true,
      handler(newHash) {
        this.activeIndex = newHash.replace('#', '');
        window.scrollTo(0, 0); // ÊªöÂä®Âà∞È°µÈù¢È°∂ÈÉ®
      }
    },
    activeIndex(newIndex) {
      // Êõ¥Êñ∞ÈîöÁÇπ
      const hash = `#${newIndex}`;
      this.$router.push({
        hash
      });
    }
  },
  components: {
    DashboardTabs,
    SysConfigTabs,
    SysCogUpload,
    SysCogSecurity,
    SysCogPage,
    SysCogOthers
  },
  computed: {
    ...mapGetters(['credentials']),
    disableTooltip() {
      return window.innerWidth < 768;
    },
    // Ê†πÊçÆÈîöÁÇπÂä®ÊÄÅËøîÂõûÂØπÂ∫îÁöÑÁªÑ‰ª∂
    currentComponent() {
      const hash = this.$route.hash.replace('#', '');
      switch (hash) {
        case 'security':
          return SysCogSecurity;
        case 'page':
          return SysCogPage;
        case 'others':
          return SysCogOthers;
        default:
          return SysCogUpload;
      }
    }
  },
  methods: {
    async fetchWithAuth(url, options = {}) {
      // ÂºÄÂèëÁéØÂ¢É, url ÂâçÈù¢Âä†‰∏ä /api
      // url = `/api${url}`;
      if (this.credentials) {
        // ËÆæÁΩÆ Authorization Â§¥
        options.headers = {
          ...options.headers,
          'Authorization': `Basic ${this.credentials}`
        };
        // Á°Æ‰øùÂåÖÂê´Âá≠ÊçÆÔºåÂ¶Ç cookies
        options.credentials = 'include';
      }
      const response = await fetch(url, options);
      if (response.status === 401) {
        // Redirect to the login page if a 401 Unauthorized is returned
        this.$message.error('ËÆ§ËØÅÁä∂ÊÄÅÈîôËØØÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï');
        this.$router.push('/adminLogin');
        throw new Error('Unauthorized');
      }
      return response;
    },
    handleLogout() {
      this.$store.commit('setCredentials', null);
      this.$router.push('/adminLogin');
    },
    // ËÆæÁΩÆÈªòËÆ§ÈîöÁÇπ
    setDefaultHash() {
      const defaultHash = '#upload'; // ÈªòËÆ§ÈîöÁÇπ
      window.location.hash = defaultHash;
      this.activeIndex = defaultHash.replace('#', '');
    }
  },
  mounted() {
    // Â¶ÇÊûú URL ‰∏≠Ê≤°ÊúâÈîöÁÇπÔºåÂàôËÆæÁΩÆÈªòËÆ§ÈîöÁÇπ
    if (!window.location.hash) {
      this.setDefaultHash();
    }
  }
};