import "core-js/modules/es.array.push.js";
import { mapGetters } from 'vuex';
export default {
  data() {
    return {
      authSettings: {
        user: {},
        admin: {}
      },
      uploadSettings: {
        moderate: {}
      },
      accessSettings: {},
      // Âä†ËΩΩÁä∂ÊÄÅ
      loading: false,
      // ‰øÆÊîπÂØÜÁ†ÅÁõ∏ÂÖ≥
      oriUserPassword: '',
      // Âéü‰∏ä‰º†ÂØÜÁ†Å
      oriAdminPassword: '',
      // ÂéüÁÆ°ÁêÜÁ´ØÂØÜÁ†Å

      showUserPassConfirm: false,
      // ÊòæÁ§∫Áî®Êà∑ÂØÜÁ†ÅÁ°ÆËÆ§Ê°Ü
      showAdminPassConfirm: false,
      // ÊòæÁ§∫ÁÆ°ÁêÜÂØÜÁ†ÅÁ°ÆËÆ§Ê°Ü

      userPassRules: {
        confirmNewUserPassword: [{
          message: 'ËØ∑ÂÜçÊ¨°ËæìÂÖ•‰∏ä‰º†ÂØÜÁ†Å',
          trigger: 'blur'
        }, {
          validator: (rule, value, callback) => {
            if (value !== this.authSettings.user.authCode) {
              callback(new Error('‰∏§Ê¨°ËæìÂÖ•ÂØÜÁ†Å‰∏ç‰∏ÄËá¥'));
            } else {
              callback();
            }
          },
          trigger: 'blur'
        }]
      },
      adminPassRules: {
        confirmNewAdminPassword: [{
          message: 'ËØ∑ÂÜçÊ¨°ËæìÂÖ•ÁÆ°ÁêÜÂØÜÁ†Å',
          trigger: 'blur'
        }, {
          validator: (rule, value, callback) => {
            if (value !== this.authSettings.admin.adminPassword) {
              callback(new Error('‰∏§Ê¨°ËæìÂÖ•ÂØÜÁ†Å‰∏ç‰∏ÄËá¥'));
            } else {
              callback();
            }
          },
          trigger: 'blur'
        }]
      }
    };
  },
  computed: {
    ...mapGetters(['credentials'])
  },
  methods: {
    async fetchWithAuth(url, options = {}) {
      // ÂºÄÂèëÁéØÂ¢É, url ÂâçÈù¢Âä†‰∏ä /api
      // url = `/api${url}`;
      if (this.credentials) {
        // ËÆæÁΩÆ Authorization Â§¥
        options.headers = {
          ...options.headers,
          'Authorization': `Basic ${this.credentials}`
        };
        // Á°Æ‰øùÂåÖÂê´Âá≠ÊçÆÔºåÂ¶Ç cookies
        options.credentials = 'include';
      }
      const response = await fetch(url, options);
      if (response.status === 401) {
        // Redirect to the login page if a 401 Unauthorized is returned
        this.$message.error('ËÆ§ËØÅÁä∂ÊÄÅÈîôËØØÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï');
        this.$router.push('/adminLogin');
        throw new Error('Unauthorized');
      }
      return response;
    },
    handleUserPassInput() {
      if (this.authSettings.user.authCode !== this.oriUserPassword) {
        this.showUserPassConfirm = true;
      } else {
        this.showUserPassConfirm = false;
      }
    },
    handleAdminPassInput() {
      if (this.authSettings.admin.adminPassword !== this.oriAdminPassword) {
        this.showAdminPassConfirm = true;
      } else {
        this.showAdminPassConfirm = false;
      }
    },
    saveSettings() {
      // ÊâÄÊúâË°®ÂçïÁöÑPromiseÊï∞ÁªÑ
      let validationPromises = [];

      // È™åËØÅÁî®Êà∑ÂØÜÁ†ÅË°®Âçï
      validationPromises.push(new Promise(resolve => {
        this.$refs.userPassForm.validate(valid => {
          resolve(valid);
        });
      }));

      // È™åËØÅÁÆ°ÁêÜÂØÜÁ†ÅË°®Âçï
      validationPromises.push(new Promise(resolve => {
        this.$refs.adminPassForm.validate(valid => {
          resolve(valid);
        });
      }));

      // Á≠âÂæÖÊâÄÊúâÈ™åËØÅÂÆåÊàê
      Promise.all(validationPromises).then(results => {
        const isValid = results.every(valid => valid);
        if (!isValid) {
          return;
        }
        const settings = {
          auth: this.authSettings,
          upload: this.uploadSettings,
          access: this.accessSettings
        };
        // ‰∏ç‰øùÂ≠òÁ°ÆËÆ§ÂØÜÁ†ÅÁõ∏ÂÖ≥Â≠óÊÆµ
        delete settings.auth.user.confirmNewUserPassword;
        delete settings.auth.admin.confirmNewAdminPassword;
        this.fetchWithAuth('/api/manage/sysConfig/security', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(settings)
        }).then(() => {
          this.$message.success('ËÆæÁΩÆÂ∑≤‰øùÂ≠ò');
        });
      });
    }
  },
  mounted() {
    this.loading = true;
    // Ëé∑Âèñ‰∏ä‰º†ËÆæÁΩÆ
    this.fetchWithAuth('/api/manage/sysConfig/security').then(response => response.json()).then(data => {
      this.authSettings = data.auth;
      this.uploadSettings = data.upload;
      this.accessSettings = data.access;

      // ‰øùÂ≠òÂéüÂØÜÁ†Å
      this.oriUserPassword = this.authSettings.user.authCode;
      this.oriAdminPassword = this.authSettings.admin.adminPassword;
      this.authSettings.user.confirmNewUserPassword = '';
      this.authSettings.admin.confirmNewAdminPassword = '';
    }).finally(() => {
      this.loading = false;
    });
  }
};