          message: 'ËØ∑ËæìÂÖ• Chat ID',
          trigger: 'blur'
        }]
      },
      // ‰∫åÁ∫ßËÆæÁΩÆÔºöCFR2 ÈÖçÁΩÆ
      cfr2Settings: {
        channels: []
      },
      // ‰∫åÁ∫ßËÆæÁΩÆÔºöS3 ÈÖçÁΩÆ
      s3Settings: {
        loadBalance: {},
        channels: []
      },
      s3Rules: {
        name: [{
          required: true,
          message: 'ËØ∑ËæìÂÖ•Ê∏†ÈÅìÂêç',
          trigger: 'blur'
        }, {
          validator: (rule, value, callback) => {
            const names = this.s3Settings.channels.map(item => item.name);
            if (names.filter(name => name === value).length > 1) {
              callback(new Error('Ê∏†ÈÅìÂêç‰∏çËÉΩÈáçÂ§ç'));
            } else if (value === 'S3_env') {
              // Âà§Êñ≠ËØ•Ê∏†ÈÅì‰øùÂ≠ò‰ΩçÁΩÆÊòØÂê¶‰∏∫ÁéØÂ¢ÉÂèòÈáè
              const savePath = this.s3Settings.channels.find(item => item.name === value).savePath;
              if (savePath !== 'environment variable') {
                callback(new Error('Ê∏†ÈÅìÂêç‰∏çËÉΩ‰∏∫‰øùÁïôÂÄº'));
              } else {
