  },
  computed: {
    ...mapGetters(['credentials']),
    // ÂΩìÂâçÈÄâ‰∏≠Ê∏†ÈÅìÁöÑÊ†áÁ≠æ
    activeChannelLabel() {
      const channel = this.channels.find(item => item.value === this.activeChannel);
      return channel ? channel.label : '';
    }
  },
  methods: {
    async fetchWithAuth(url, options = {}) {
      // ÂºÄÂèëÁéØÂ¢É, url ÂâçÈù¢Âä†‰∏ä /api
      // url = `/api${url}`;
      if (this.credentials) {
        // ËÆæÁΩÆ Authorization Â§¥
        options.headers = {
          ...options.headers,
          'Authorization': `Basic ${this.credentials}`
        };
        // Á°Æ‰øùÂåÖÂê´Âá≠ÊçÆÔºåÂ¶Ç cookies
        options.credentials = 'include';
      }
      const response = await fetch(url, options);
      if (response.status === 401) {
        // Redirect to the login page if a 401 Unauthorized is returned
        this.$message.error('ËÆ§ËØÅÁä∂ÊÄÅÈîôËØØÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï');
        this.$router.push('/adminLogin');
        throw new Error('Unauthorized');
      }
      return response;
    },
    addChannel() {
      switch (this.activeChannel) {
        case 'telegram':
          this.telegramSettings.channels.push({
            id: this.telegramSettings.channels.length + 1,
            name: '',
            type: 'telegram',
            savePath: 'database',
            botToken: '',
            chatId: '',
            enabled: true,
            fixed: false
          });
          break;
        case 'cfr2':
          // this.cfr2Settings.channels.push({
          //     id: this.cfr2Settings.channels.length + 1,
          //     name: '',
          //     type: 'cfr2',
          //     savePath: 'database',
          //     enabled: true,
          //     fixed: false
          // });
          this.$message.error('ÊöÇ‰∏çÊîØÊåÅÂ¢ûÂä† R2Ê∏†ÈÅì ÈÖçÁΩÆ');
          break;
        case 's3':
          this.s3Settings.channels.push({
            id: this.s3Settings.channels.length + 1,
            name: '',
            type: 's3',
            savePath: 'database',
            accessKeyId: '',
            secretAccessKey: '',
            region: '',
            bucketName: '',
            endpoint: '',
            enabled: true,
            fixed: false
          });
          break;
      }
    },
    deleteChannel(index) {
      switch (this.activeChannel) {
        case 'telegram':
          // Ë∞ÉÊï¥ id
          this.telegramSettings.channels.forEach((item, i) => {
            if (i > index) {
              item.id -= 1;
            }
          });
          this.telegramSettings.channels.splice(index, 1);
          break;
        case 'cfr2':
          // Ë∞ÉÊï¥ id
          this.cfr2Settings.channels.forEach((item, i) => {
            if (i > index) {
              item.id -= 1;
            }
          });
          this.cfr2Settings.channels.splice(index, 1);
          break;
        case 's3':
          // Ë∞ÉÊï¥ id
          this.s3Settings.channels.forEach((item, i) => {
            if (i > index) {
              item.id -= 1;
            }
          });
          this.s3Settings.channels.splice(index, 1);
          break;
      }
    },
    saveSettings() {
      // ÊâÄÊúâË°®ÂçïÁöÑ Promise Êï∞ÁªÑ
      let validationPromises = [];

      // Telegram
      if (this.$refs.tgChannelForm) {
        this.$refs.tgChannelForm.forEach(form => {
          validationPromises.push(new Promise(resolve => {
            form.validate(valid => resolve(valid));
          }));
        });
      }

      // S3
      if (this.$refs.s3ChannelForm) {
        this.$refs.s3ChannelForm.forEach(form => {
          validationPromises.push(new Promise(resolve => {
            form.validate(valid => resolve(valid));
          }));
        });
      }

      // Á≠âÂæÖÊâÄÊúâÈ™åËØÅÂÆåÊàê
      Promise.all(validationPromises).then(results => {
        const isValid = results.every(valid => valid);
        if (!isValid) {
          return;
        }

        // ‰øùÂ≠òËÆæÁΩÆ
        const settings = {
          telegram: this.telegramSettings,
          cfr2: this.cfr2Settings,
          s3: this.s3Settings
        };
        this.fetchWithAuth('/api/manage/sysConfig/upload', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(settings)
        }).then(() => {
          this.$message.success('ËÆæÁΩÆÂ∑≤‰øùÂ≠ò');
        });
      });
    },
    // Ê†πÊçÆÊ∏†ÈÅìÁ±ªÂûãËøîÂõûÂØπÂ∫îÁöÑË°®ÊÉÖÁ¨¶Âè∑
    getChannelEmoji(channelType) {
      const emojiMap = {
        'telegram': 'ü§ñ',
        'cfr2': '‚òÅÔ∏è',
        's3': 'üì¶'
      };
      return emojiMap[channelType] || 'üì§';
    }
  },
  mounted() {
    this.loading = true;
    // Ëé∑Âèñ‰∏ä‰º†ËÆæÁΩÆ
    this.fetchWithAuth('/api/manage/sysConfig/upload').then(response => response.json()).then(data => {
      this.telegramSettings = data.telegram;
      this.cfr2Settings = data.cfr2;
      this.s3Settings = data.s3;
    }).finally(() => {
      this.loading = false;
    });
  }
};